<template>
    <div class="map-root">
        <div id="YMapsID" class="map-img"/>
    </div>
</template>

<script>
window.ymaps.ready(function () {
    const myMap = new window.ymaps.Map("YMapsID", {
      center: [54.653902, 20.541093],
      zoom: 17
    });
    var myGeoObject = new window.ymaps.GeoObject({
        geometry: {
            type: "Point", // тип геометрии - точка
            coordinates: [54.653902, 20.541093] // координаты точки
        },
        properties: {
            hintContent: "ООО План Б"
        }
    });
    myMap.geoObjects.add(myGeoObject); 
  });

export default {
    data() {
        return {
            settings: {
                apiKey: '',
                lang: 'ru_RU',
                coordorder: 'latlong',
                version: '2.1'
            }
        }
    }
}
</script>
<style scoped>
.map-root {
    display:flex;
    justify-content:center;
    margin-top: 20px;
    margin-bottom: 40px;
}
.map-img {
    width: 100%;
    max-width: 1150px;
    margin: 0 auto;
    height: 350px;
}
</style>